<template>
  <div class="class-menu">
    <a-menu
      mode="inline"
      theme="dark"
      :selectable="false"
      :open-keys="openKeys"
      @openChange="onOpenChange"
      :inline-collapsed="isCollapse"
    >
      <template v-for="item in list">
        <template v-if="!item.children">
          <a-menu-item :key="item.path" @click="routerClick(item)">{{item.meta.name}}</a-menu-item>
        </template>
        <a-sub-menu :key="item.path" v-else>
          <span slot="title">
            <a-icon type="appstore" />
            <span>{{item.meta.title}}</span>
          </span>
          <a-menu-item
            v-for="subItem in item.children"
            :key="subItem.path"
            @click="routerClick(subItem)"
          >{{subItem.meta.name}}</a-menu-item>
        </a-sub-menu>
      </template>
    </a-menu>
  </div>
</template>

<script>
export default {
  props: {
    isCollapse: Boolean
  },
  data() {
    return {
      list: [],
      rootSubmenuKeys: this.$router.options.routes[0].children,
      openKeys: []
    };
  },
  mounted() {
    this.list = this.$router.options.routes[0].children;
  },
  methods: {
    // 跳转路由
    routerClick(route) {
      this.$router.push(route.path);
    },
    // 只展开一个菜单
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        key => this.openKeys.indexOf(key) === -1
      );
      let arr = [];
      this.rootSubmenuKeys.forEach(item => {
        arr.push(item.path);
      });
      if (arr.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
<style>
.left-icon {
  width: 20px;
  height: 20px;
}
.ant-menu-root.ant-menu-inline {
  width: 200px;
  background-color: #15253a;
}
.ant-menu-dark,
.ant-menu-dark .ant-menu-sub {
  background-color: #15253a;
}
/* .ant-menu-dark,
.ant-menu-inline,
.ant-menu-sub {
  background: #101d2e !important;
} */
</style>



