<template>
  <div class="home">
    <el-container>
      <!-- header -->
      <el-header>
        <div class="header-layout">
          <div class="left-layout">
            <div v-if="isCollapse" class="isLeftNav is-left-nav">
              <i :class="className"></i>
            </div>
            <div :class="titleClassStyle?'header-title':'title-back'">唐山共享交换平台</div>
            <div v-if="!isHeader&&!isCollapse&&leftLayout" class="isLeftNav">
              <i :class="className"></i>
            </div>
            <div
              v-if="!isHeader&&!isCollapse&&leftLayout"
              :class="classGateway"
              v-on:mouseover="gatewayClick"
              v-on:mouseout="removeActive"
            >
              <i class="el-icon-s-grid"></i>
              <span>我的门户</span>
              <!-- 门户导航 -->
              <div v-show="isGateway" class="header-nav">
                <div v-for="(item,index) in navList" :key="index">
                  <img :src="item.icon" />
                  <span>{{item.user}}</span>
                </div>
              </div>
            </div>
            <div class="query-input">
              <span>人员</span>
              <input type="text" placeholder="请输入关键词查询" />
              <i class="el-icon-search"></i>
            </div>
          </div>
          <div class="user-info">
            <div class="head-portrait"></div>
            <div class="user-name" @click="suerClick">
              <span>刘亚斌</span>
              <i class="el-icon-arrow-down"></i>
            </div>
            <ul v-show="isSetUp" class="user-setUp">
              <li @click="modifyClick">修改密码</li>
              <li @click="layoutClick">布局选择</li>
              <li @click="returnClick">推出登录</li>
            </ul>
          </div>
        </div>
      </el-header>
      <!-- 显示头部导航 -->
      <header class="nav-header" v-if="isHeader">
        <div class="left-icon">
          <i class="el-icon-arrow-left"></i>
        </div>
        <div class="center-nav-icon">
          <ul>
            <li
              :class="haedNavIndex===index?'activeNavStyle':''"
              v-for="(item,index) in navList"
              :key="index"
              @click="headerClick(index)"
            >
              <i :class="item.icon"></i>
              <span>{{item.user}}</span>
            </li>
          </ul>
        </div>
        <div class="right-icon">
          <i class="el-icon-arrow-right"></i>
        </div>
      </header>
      <!-- content -->
      <div class="center-lauty">
        <div class="left-nav">
          <left-nav :isCollapse="isCollapse"></left-nav>
        </div>
        <section class="section-content">
          <div v-if="this.$route.path!=='/index'" class>当前位置：</div>
          <router-view></router-view>
        </section>
      </div>
      <!-- footer -->
      <el-footer>
        <footer-content></footer-content>
      </el-footer>
    </el-container>
    <!-- 选择布局弹框 -->
    <el-dialog title="布局选择" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
      <div class="layout-style">
        <div
          :class="isLayout===1?'layout-item active-layout':'layout-item'"
          @click="layoutStyleClick(1)"
        ></div>
        <div
          :class="isLayout===2?'layout-item active-layout':'layout-item'"
          @click="layoutStyleClick(2)"
        ></div>
        <div
          :class="isLayout===3?'layout-item active-layout':'layout-item'"
          @click="layoutStyleClick(3)"
        ></div>
        <div
          :class="isLayout===4?'layout-item active-layout':'layout-item'"
          @click="layoutStyleClick(4)"
        ></div>
      </div>
      <div class="layout-style">
        <div class="layout-text">默认显示</div>
        <div class="layout-text">顶部栏展示</div>
        <div class="layout-text">侧边栏展示</div>
        <div class="layout-text">极简模式</div>
      </div>
    </el-dialog>
    <!-- 添加日程弹框 -->
    <add-schedule :isFlag="isSchedule"></add-schedule>
  </div>
</template>

<script>
import LeftNav from "@/components/leftNav";
import FooterContent from "@/components/footer";
import AddSchedule from "@/components/addSchedule";
export default {
  name: "Home",
  components: {
    LeftNav,
    FooterContent,
    AddSchedule
  },
  data() {
    return {
      isCollapse: false,
      className: "el-icon-s-unfold",
      isGateway: false,
      classGateway: "icon-nav",
      isSetUp: false,
      dialogVisible: false,
      isLayout: 1,
      haedNavIndex: 0,
      isHeader: false,
      leftLayout: true,
      titleClassStyle: true,
      isSchedule: false,
      navList: [
        {
          icon: require("@/assets/homeSvg/leftNav/信息安全与支撑平台.svg"),
          user: "信息安全与支撑平台"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/信息资源共享交换.svg"),
          user: "信息资源共享交换"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/共享网站.svg"),
          user: "共享网站"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/大数据平台.svg"),
          user: "大数据平台"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/中心库管理平台.svg"),
          user: "中心库管理平台"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/运行管理平台.svg"),
          user: "运行管理平台"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/微服务平台.svg"),
          user: "微服务平台"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/审计日志系统.svg"),
          user: "审计日志系统"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/数据开放网站.svg"),
          user: "数据开放网站"
        },
        {
          icon: require("@/assets/homeSvg/leftNav/基础数据库.svg"),
          user: "基础数据库"
        }
      ]
    };
  },
  created() {},
  mounted() {},
  watch: {
    isCollapse(val) {
      if (val) {
        this.className = "el-icon-s-unfold";
      } else {
        this.className = "el-icon-s-fold";
      }
    }
  },
  methods: {
    // 头部导航
    headerClick(index) {
      this.haedNavIndex = index;
    },
    // 控制门户导航
    gatewayClick() {
      this.isGateway = true;
      this.classGateway = "active-icon";
    },
    removeActive() {
      this.isGateway = false;
      this.classGateway = "icon-nav";
    },
    // 点击用户名
    suerClick() {
      this.isSetUp = !this.isSetUp;
    },

    // 修改密码
    modifyClick() {},
    // 选择布局
    layoutClick() {
      this.dialogVisible = true;
      this.isSetUp = false;
    },
    layoutStyleClick(num) {
      if (num === 1) {
        //默认布局
        this.isLayout = 1;
        this.isHeader = false;
        this.isCollapse = false;
        this.leftLayout = true;
        this.titleClassStyle = true;
        this.dialogVisible = false;
      } else if (num === 2) {
        //顶部栏展示
        this.isLayout = 2;
        this.isHeader = true;
        this.isCollapse = false;
        this.leftLayout = true;
        this.titleClassStyle = true;
        this.dialogVisible = false;
      } else if (num === 3) {
        //侧边栏展示
        this.isLayout = 3;
        this.isHeader = false;
        this.isCollapse = false;
        this.leftLayout = false;
        this.titleClassStyle = true;
        this.dialogVisible = false;
      } else if (num === 4) {
        //极简模式
        this.isLayout = 4;
        this.isCollapse = true;
        this.isHeader = false;
        this.leftLayout = true;
        this.titleClassStyle = false;
        this.dialogVisible = false;
      }
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // 推出登录
    returnClick() {}
  }
};
</script>

<style lang="scss" scoped>
.home {
  background: $home-background-color;
  .center-lauty {
    display: flex;
    .left-nav {
      background: #15253a;
    }
    .section-content {
      flex: 1;
    }
  }
  .nav-header {
    height: 40px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    .left-icon,
    .right-icon {
      font-size: 30px;
      background: #fff;
    }
    .center-nav-icon {
      flex: 1;
      overflow-y: visible;
      ul {
        width: 200%;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        margin-top: 15px;
        li {
          margin: 0 5px;
          padding: 5px;
        }
        .activeNavStyle {
          background: #008df7;
          color: #fff;
          border-radius: 2px;
        }
      }
    }
  }
  .header-layout {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    .left-layout {
      display: flex;
      align-items: center;
    }
    .header-title {
      background: #008df7;
      line-height: 60px;
      width: 200px;
      text-align: center;
      color: $font-color-white;
    }
    .title-back {
      background-color: #0270c1;
      line-height: 60px;
      width: 200px;
      text-align: center;
      color: $font-color-white;
    }
    .isLeftNav {
      width: 64px;
      height: 60px;
      text-align: center;
      line-height: 60px;
      font-size: 30px;
      color: $font-color-white;
    }
    .is-left-nav {
      width: 80px;
      background-color: #008df7;
    }
    .icon-nav {
      color: $font-color-white;
      display: flex;
      align-items: center;
      padding: 15px 10px;
      i {
        font-size: 20px;
      }
      span {
        font-size: 16px;
      }
    }
    .active-icon {
      padding: 18px 10px;
      color: $font-color-long;
      background: $long-background-color;
      position: relative;
      i {
        font-size: 20px;
      }
      span {
        font-size: 16px;
      }

      .header-nav {
        width: 430px;
        height: 190px;
        position: absolute;
        left: 0;
        top: 60px;
        background: $long-background-color;
        z-index: 100;
        display: flex;
        flex-wrap: wrap;
        padding: 20px 10px;
        box-sizing: border-box;
        box-shadow: 0px 5px 5px #d9d9d9;
        div {
          width: 70px;
          height: 70px;
          margin-left: 10px;
          margin-bottom: 10px;
          background: #0270c1;
          padding: 5px 5px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: center;
          img {
            width: 20px;
          }
          span {
            flex: 1;

            font-size: 12px;
            color: #fff;
            display: flex;
            align-items: center;
            text-align: center;
          }
        }
      }
    }
    .query-input {
      display: flex;
      align-items: center;
      border: 1px solid #fff;
      color: #fff;
      padding: 5px 10px;
      margin-left: 15px;
      span {
        border-right: 1px solid #fff;
        padding-right: 10px;
        margin-right: 10px;
      }
    }
    .user-info {
      display: flex;
      align-items: center;
      color: #fff;
      position: relative;
      .head-portrait {
        width: 30px;
        height: 30px;
        background: #fff;
        border-radius: 30px;
        margin-right: 10px;
      }
      .user-setUp {
        background: #fff;
        position: absolute;
        color: #343434;
        top: 35px;
        right: -10px;
        width: 100px;
        height: 90px;
        line-height: 28px;
        border-radius: 2px;
        z-index: 100;
        border: 0.5px solid #d9d9d9;
        text-align: center;
        :after {
          content: "";
          width: 0;
          height: 0;
          position: absolute;
          top: -8px;
          right: 10px;
          border-width: 0 8px 8px;
          border-style: solid;
          border-color: transparent transparent #fff;
        }
      }
    }
  }

  .layout-style {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
    .layout-item {
      width: 100px;
      height: 110px;
      border: 0.5px solid #d5d5d5;
    }
    .active-layout {
      border-color: #008df7;
      box-shadow: 0 0 5px #008df7;
    }
    .layout-text {
      color: #222222;
      font-weight: bold;
    }
  }
}
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #fff;
}
.el-header,
.el-footer {
  background-color: $header-background-color;
  color: #333;
  // text-align: center;
  height: 82px;
  padding: 0;
}
.el-footer {
  height: 218px !important;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: $home-background-color;
  color: #333;
  text-align: center;
  height: auto;
}

body > .el-container {
  margin-bottom: 40px;
  // @include w_h_size;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>



