<template>
  <div class="schedule-from">
    <el-dialog title="新建日程" :visible.sync="dialogFormVisible">
      <div class="dialog-schedule">
        <el-form :model="form" label-position="left">
          <el-form-item label="标题:" :label-width="formLabelWidth">
            <div class="from-style">
              <el-input v-model="form.name" autocomplete="off"></el-input>
            </div>
          </el-form-item>
          <el-form-item label="内容:" :label-width="formLabelWidth">
            <div class="from-style">
              <el-input type="textarea" v-model="form.name" autocomplete="off"></el-input>
            </div>
          </el-form-item>
          <el-form-item label="紧急程度:" :label-width="formLabelWidth">
            <el-select v-model="form.value" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="开始时间:" :label-width="formLabelWidth">
            <el-date-picker v-model="form.startTime" type="date" placeholder="选择开始时间"></el-date-picker>
          </el-form-item>
          <el-form-item label="结束时间:" :label-width="formLabelWidth">
            <el-date-picker v-model="form.endTime" type="date" placeholder="选择结束时间"></el-date-picker>
          </el-form-item>
          <el-form-item label="提醒方式:" :label-width="formLabelWidth">
            <el-checkbox-group v-model="form.checkList">
              <el-checkbox label="短信提醒"></el-checkbox>
              <el-checkbox label="邮件提醒"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="开始前提醒:" :label-width="formLabelWidth">
            <div class="remind-style">
              <el-input v-model="form.remind" autocomplete="off"></el-input>
              <span>分钟</span>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submit">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        name: "",
        value: "",
        startTime: "",
        endTime: "",
        checkList: [],
        remind: ""
      },
      formLabelWidth: "90px",
      options: [
        {
          value: "选项1",
          label: "紧急"
        },
        {
          value: "选项2",
          label: "一般"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        }
      ]
    };
  },
  computed: {
    isScheduleVuex() {
      return this.$store.state.isSchedule;
    }
  },
  watch: {
    isScheduleVuex(curVal, oldVal) {
      //这里的curVal就是需要监听的值
      this.dialogFormVisible = curVal;
    }
  },
  methods: {
    submit() {
      this.$store.dispatch("scheduleFrom", false);
    }
  }
};
</script>
<style lang="scss" scope>
.schedule-from {
  .dialog-schedule {
    margin-left: 50px;
  }
  .from-style {
    width: 70%;
  }
  .remind-style {
    width: 200px;
    height: 100%;
    display: flex;
    align-items: center;
    span {
      width: 50px;
      margin-left: 10px;
    }
  }
  .dialog-footer {
    display: flex;
    justify-content: center;
  }
}
</style>
<style>
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 70%;
}
.el-select {
  width: 70%;
}
</style>



