import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
        path: '/',
        name: 'Home',
        component: Home,
        redirect: '/index',
        children: [
            // 首页
            {
                path: '/index',
                meta: {
                    title: '首页',
                    name: '首页'
                },
                component: () =>
                    import ('@/views/homePage')
            },
            // 我的办事
            {
                path: '/business',
                meta: {
                    title: '我的办事',
                    name: '我的办事'
                },
                component: () =>
                    import ('@/views/main'),
                children: [{
                        path: '/business/todo',
                        meta: {
                            title: '待办事项',
                            name: '待办事项'
                        },
                        component: () =>
                            import ('@/views/toDo')
                    },
                    {
                        path: '/business/already',
                        meta: {
                            title: '已办事项',
                            name: '已办事项'
                        },
                        component: () =>
                            import ('@/views/task/already')
                    },
                    {
                        path: '/business/taskComplete',
                        meta: {
                            title: '已办任务',
                            name: '已办任务'
                        },
                        component: () =>
                            import ('@/views/task/complete')
                    },
                    {
                        path: '/business/waitfor',
                        meta: {
                            title: '代办任务',
                            name: '代办任务'
                        },
                        component: () =>
                            import ('@/views/task/waitFor')
                    },
                    {
                        path: '/business/waitForMatter',
                        meta: {
                            title: '待阅事项',
                            name: '待阅事项'
                        },
                        component: () =>
                            import ('@/views/read/waitFor')
                    },
                    {
                        path: '/business/complete',
                        meta: {
                            title: '已阅事项',
                            name: '已阅事项'
                        },
                        component: () =>
                            import ('@/views/read/complete')
                    }
                ]
            },
            // 数据驾驶舱
            {
                path: '/datacockpit',
                meta: {
                    title: '数据驾驶舱',
                    name: '数据驾驶舱'
                },
                component: () =>
                    import ('@/views/main'),
                children: [{
                    path: '/datacockpit/cockpit',
                    meta: {
                        title: '驾驶舱',
                        name: '驾驶舱'
                    },
                    component: () =>
                        import ('@/views/cockpit')
                }]
            },
            // 通讯录
            {
                path: '/mailList',
                meta: {
                    title: '通讯录',
                    name: '通讯录'
                },
                component: () =>
                    import ('@/views/main'),
                children: [{
                    path: '/mailList/see',
                    meta: {
                        title: '通讯录查看',
                        name: '通讯录查看'
                    },
                    component: () =>
                        import ('@/views/mailList')
                }]
            },
            // 信息中心
            {
                path: '/information',
                meta: {
                    title: '信息中心',
                    name: '信息中心'
                },
                component: () =>
                    import ('@/views/main'),
                children: [{
                    path: '/information/station',
                    meta: {
                        title: '站内消息',
                        name: '站内消息'
                    },
                    component: () =>
                        import ('@/views/information/station')
                }, {
                    path: '/information/notice',
                    meta: {
                        title: '通知公告',
                        name: '通知公告'
                    },
                    component: () =>
                        import ('@/views/information/notice')
                }, {
                    path: '/information/remind',
                    meta: {
                        title: '消息提醒',
                        name: '消息提醒'
                    },
                    component: () =>
                        import ('@/views/information/remind')
                }, ]
            },
            // 个人信息
            {
                path: '/personal',
                meta: {
                    title: '个人信息',
                    name: '个人信息'
                },
                component: () =>
                    import ('@/views/main'),
                children: [{
                        path: '/personal/information',
                        meta: {
                            title: '个人信息',
                            name: '个人信息'
                        },
                        component: () =>
                            import ('@/views/personal/information')
                    },
                    {
                        path: '/personal/password',
                        meta: {
                            title: '个人信息',
                            name: '修改密码'
                        },
                        component: () =>
                            import ('@/views/personal/password')
                    }
                ]
            }
        ]
    }]
})


